.products {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  list-style-type: none;
  column-gap: 10px;
  row-gap: 10px;
  margin: 30px 0px 0px 0px;
}

.img {
  width: 60%;
}

.img:hover {
  background-color: #fafafa;
  filter: brightness(105%);
}

.name {
  font-weight: bold;
}
